nav {
    height: 90px;
    background: #2d2d32;
    transition: 0.3s;
    text-decoration: none;
    font-family: sans-serif;
    padding: 2rem 2rem;
}

nav.sticky {
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.6);
    position: sticky;
    top: 0;
    z-index: 100;
}

.nav-inner {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1300px;
    width: 100%;
    margin: auto;
}

.logo {
    color: #fff;
    font-size: 30px;
}

.links a {
    color: #fff;
    font-size: 20px;
    margin-right: 30px;
    cursor: pointer;
    text-decoration: none;
}

@media screen and (max-width:550px) {
    .links a {
        font-size: 16px;
        margin-right: 18px;
    }

    .logo {
        font-size: 20px;
    }
}

@media screen and (max-width:400px) {
    .links a {
        font-size: 12px;
        margin-right: 16px;
    }

    .logo {
        font-size: 18px;
    }


}

@media screen and (max-width:300px) {
    .links a {
        font-size: 10px;
        margin-right: 12px;
    }

}