.content {
  height: 100vh;
}

.content a {
  text-decoration: none;
  color: white;
}

.content a:hover {
  color: black;
}

.text-content {
  font-family: Arial, Helvetica, sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 5px 20px;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
  background-color: rgba(255, 255, 255, .15);
  backdrop-filter: blur(5px);
  color: white;
  border-radius: 30px;
  font-size: 50px;

}

.container {

  margin-right: auto;
  margin-left: auto;

}